import React from 'react'
import { Link } from 'react-router-dom'
const BtnLink = ({path,btnIcon,btnText,style,onClick}) => {
  return (
    <Link 
    to={path} 
    style={style} 
    onClick={onClick}
    className="bg-mainColor hover:bg-mainColorAlt first-letter:capitalize text-white cursor-pointer btnAction flex items-center gap-3  lg:gap-2  p-[10px] lgg:p-[15px] rounded-md  lg:text-sm font-medium">
      <div 
      className="btnIcon">{btnIcon}</div> {btnText}</Link>
  )
}

export default BtnLink