import React, { useEffect, useState } from 'react'
import User from '../../iconSvg/User'
import Search from '../../iconSvg/Search'
import BtnSubmit from '../ui/BtnSubmit'
import 'react-calendar/dist/Calendar.css';
import CalendarIcon from '../../iconSvg/CalendarIcon'
import Calendar from 'react-calendar';
import Minus from '../../iconSvg/Minus';
import Plus from '../../iconSvg/Plus';
import ArrowLeft from '../../iconSvg/ArrowLeft';
import ArrowRight from '../../iconSvg/ArrowRight';
import Moment from 'moment';
import Seat from '../../iconSvg/Seat';
import BtnLink from '../ui/BtnLink';
import Star from '../../iconSvg/Star';
import Close from '../../iconSvg/Close';
import PointDepart from '../../iconSvg/PointDepart';
import PointDarrive from '../../iconSvg/PointDarrive';
import Map from '../../iconSvg/Map';
import axios from 'axios';
import Historique from '../../iconSvg/Historique';
import { voyageDispoData } from '../../data/Data';
const BannerHomePage = () => {
    const PF = process.env.REACT_APP_PUBLIC_FOLDER
    const [date, setDate] = useState(new Date());
    const [axiosError,setAxiosError]=useState({isThereIsError:false,errorContent:""})
    var currentDate = new Date();
    var numberOfDaysToAdd = 30;
    var result = currentDate.setDate(currentDate.getDate() + numberOfDaysToAdd);
    const maxDate = new Date(result)
    const [valueToIncrement, setValueToIncrement] = useState(0);
    const [suggestedVillesDepart, setSuggestedVillesDepart] = useState([]);
    const [suggestedVillesDarrive, setSuggestedVillesDarrive] = useState([]);
    const [villeData, setVilleData] = useState([]);
    const [showDispoVoyage, setShowDispoVoyage] = useState(false);
    const [showRequiredMsg, setShowRequiredMsg] = useState(false);
    const [openedDropdown, setOpenedDropdown] = useState({
        ndropdown: -1,
        villeDepart: "",
        villeDArrive: "",
        nbvoyageur: 0,
    })
    
    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await axios.get("https://jsonplaceholder.typicode.com/todos")
                setVilleData(res.data)
            } catch (error) {
                setAxiosError({
                    isThereIsError:true,
                    errorContent:error.message
                })
                console.log(error.message)
            }
        }
        fetchData()
    }, [])
    // algorithm to take info from historique : 
    // if i make foccus on input it shows me suggestion of places that i al ready choose
    // every time that i choose a place it pushed to an array in localstorage
    const voyageSearchFields = [
        {
            title: openedDropdown.villeDepart,
            name: "villeDepart",
            icon: <PointDepart className=" w-[23px] h-[23px] fill-white" />,
            dropDownContent:
                <div style={{ height: suggestedVillesDepart.length >= 4 ? "250px" : "max-content" }} className={suggestedVillesDepart.length !== 0
                    ? "show searchBottom  overflow-hidden overflow-y-scroll z-[30] absolute p-5 bg-lightGray w-full lg:w-[calc(100%+200px)] xl:w-full "
                    : "hide searchBottom  overflow-hidden overflow-y-scroll z-[30] absolute p-5 bg-lightGray w-full lg:w-[calc(100%+200px)] xl:w-full "
                }>
                    {
                        suggestedVillesDepart.map((ville, j) => (
                            <div
                                onClick={() => {
                                    setTimeout(() => {
                                        setOpenedDropdown({ ...openedDropdown, villeDepart: ville.title, ndropdown: -1 })
                                    }, 100);
                                }
                                }
                                key={j}
                                className="searchBottomItem flex items-center gap-[10px] last:border-0 cursor-pointer hover:text-mainColor transition-all border-b-[1px] border-[lightgray] py-[5px] last:pb-0 first:pt-0 ">
                                {
                                    openedDropdown.villeDepart === ''
                                        ? <Historique className=" w-[18px] h-[18px] fill-mainColor" />
                                        : <Map className=" w-[18px] h-[18px] fill-mainColor" />
                                }
                                <span className="w-[calc(100%-28px)] ">{ville.title}</span>
                            </div>
                        ))
                    }
                </div>,
        }, {
            title: openedDropdown.villeDArrive,
            name: "villeDArrive",
            icon: <PointDarrive className=" w-[23px] h-[23px] fill-white" />,
            dropDownContent:
                <div style={{ height: suggestedVillesDarrive.length >= 4 ? "250px" : "max-content" }} className={suggestedVillesDarrive.length !== 0
                    ? "show searchBottom  overflow-hidden overflow-y-scroll z-[30] absolute p-5 bg-lightGray w-full lg:w-[calc(100%+200px)] xl:w-full "
                    : "hide searchBottom  overflow-hidden overflow-y-scroll z-[30] absolute p-5 bg-lightGray w-full lg:w-[calc(100%+200px)] xl:w-full "}>
                    {
                        suggestedVillesDarrive.map((ville, j) => (
                            <div
                                onClick={() => {
                                    setTimeout(() => {
                                        setOpenedDropdown({ ...openedDropdown, villeDArrive: ville.title, ndropdown: -1 })
                                    }, 100);
                                }
                                }
                                key={j}
                                className="searchBottomItem flex items-center gap-[10px] last:border-0 cursor-pointer hover:text-mainColor transition-all border-b-[1px] border-[lightgray] py-[5px] last:pb-0 first:pt-0 ">
                                {
                                    openedDropdown.villeDArrive === ''
                                        ? <Historique className=" w-[18px] h-[18px] fill-mainColor" />
                                        : <Map className=" w-[18px] h-[18px] fill-mainColor" />
                                }
                                <span className="w-[calc(100%-28px)] ">{ville.title}</span>
                            </div>
                        ))
                    }
                </div>,
        },
        {
            title: valueToIncrement < 2 ? "date de depart" : Moment(date).format('DD/MM/yyyy'),
            icon: <CalendarIcon className=" w-[18px] h-[18px] fill-white" />,
            dropDownContent:
                <div className="searchBottom absolute bg-lightGray z-[30] left-[50%] translate-x-[-50%] w-full md:w-[calc(100%-200px)] lg:w-[calc(100%+100px)]">
                    <Calendar minDate={new Date()} onChange={setDate} value={date} />
                    <div className="arrowControllers pointer-events-none absolute w-full flex justify-between px-[25px] py-[30px]">
                        <ArrowLeft className="w-5" />
                        <ArrowRight className="w-5" />
                    </div>
                </div>
        },
        {
            title: openedDropdown.nbvoyageur === 0 ? "nombre de voyageurs" : openedDropdown.nbvoyageur,
            icon: <User className=" w-[18px] h-[18px] fill-white" />,
            dropDownContent:
                <div className="searchBottom z-[30] absolute p-5 bg-lightGray w-full ">
                    <div className="flex items-center justify-between">
                        Passager
                        <div className="nbControler flex items-center gap-[20px] ">
                            <Minus
                                onClick={() => {
                                    if (openedDropdown.nbvoyageur > 1) {
                                        setTimeout(() => {
                                            setOpenedDropdown({ ...openedDropdown, nbvoyageur: openedDropdown.nbvoyageur - 1 })
                                        }, 100);
                                    }

                                }
                                }
                                className={openedDropdown.nbvoyageur < 1
                                    ? " opacity-50 cursor-not-allowed w-[18px] h-[18px]"
                                    : "cursor-pointer w-[18px] h-[18px]"
                                } />
                            <span >{openedDropdown.nbvoyageur}</span>
                            <Plus
                                onClick={() => {
                                    if (openedDropdown.nbvoyageur <= 10) {
                                        setTimeout(() => {
                                            setOpenedDropdown({ ...openedDropdown, nbvoyageur: openedDropdown.nbvoyageur + 1 })
                                        }, 100);
                                    }

                                }
                                }
                                className={openedDropdown.nbvoyageur > 10
                                    ? " opacity-50 cursor-not-allowed w-[18px] h-[18px]"
                                    : "cursor-pointer w-[18px] h-[18px]"
                                } />
                        </div>
                    </div>
                </div>,
        },
    ]
    useEffect(() => {
        setValueToIncrement(valueToIncrement + 1)
        setOpenedDropdown({ ...openedDropdown, ndropdown: -1 })
    }, [Moment(date).format('DD/MM/yyyy')])
    window.onclick = (e) => {
        if (document.getElementById("clickBox")?.contains(e.target)) {
            console.log("click in the box ")
        } else {
            setOpenedDropdown({ ...openedDropdown, ndropdown: -1 })
        }
    }

    function handleChange(e) {
        setOpenedDropdown({ ...openedDropdown, [e.target.name]: e.target.value })
    }
    useEffect(() => {
        setSuggestedVillesDepart(villeData.filter((ville) => ville.title.toLowerCase().includes(openedDropdown.villeDepart)))
        if (openedDropdown.villeDepart === "") {
            // alert("openedDropdown.villeDepart === empty")
            if (localStorage.getItem('villeDepart') === null) {
                alert("localStorage.getItem('villeDepart') === null")
                setSuggestedVillesDepart([])
            } else {
                alert("localStorage.getItem('villeDepart') is not null")
                setSuggestedVillesDepart(JSON.parse(localStorage.getItem('villeDepart')))
            }

        }
    }, [openedDropdown.villeDepart])
    useEffect(() => {
        setSuggestedVillesDarrive(villeData.filter((ville) => ville.title.toLowerCase().includes(openedDropdown.villeDArrive)))
        if (openedDropdown.villeDArrive === "") {
            if (localStorage.getItem('villeDArrive') === null) {
                setSuggestedVillesDarrive([])
            } else {
                setSuggestedVillesDarrive(JSON.parse(localStorage.getItem('villeDArrive')))
            }
        }
    }, [openedDropdown.villeDArrive])
    // onclick btn rechercher events 
    if (localStorage.getItem('villeDepart') === null) {
        localStorage.setItem('villeDepart', '[]')
    }
    var oldDataVilleDepart = JSON.parse(localStorage.getItem('villeDepart'))
    // ville d'arrivee 
    if (localStorage.getItem('villeDArrive') === null) {
        localStorage.setItem('villeDArrive', '[]')
    }
    var oldDataVilleDarrivee = JSON.parse(localStorage.getItem('villeDArrive'))
    const handleSubmit = (e) => {
        console.log("search.. ", openedDropdown.villeDepart, openedDropdown.villeDArrive)
        e.preventDefault()
        if (openedDropdown.villeDepart !== '' && openedDropdown.villeDArrive !== '') {
            setShowRequiredMsg(false)
            setOpenedDropdown({ ...openedDropdown, ndropdown: -1 })
            setShowDispoVoyage(true)
            // ********* ville depart *************
            var checkValue = 0
            if (oldDataVilleDepart.length !== 0) {
                console.log("oldDataVilleDepart is full")
                for (let i = 0; i < oldDataVilleDepart.length; i++) {
                    console.log("checkValue", checkValue)
                    console.log("**************** ", openedDropdown.villeDepart, "****** ", oldDataVilleDepart[i].title.toLowerCase())
                    if (oldDataVilleDepart[i].title.toLowerCase().includes(openedDropdown.villeDepart)) {

                        console.log("localStorage doesn't include selected place")
                        // setCheckValue(checkValue+1)
                        checkValue++
                        console.log("increment", checkValue)
                    }
                }
                if (checkValue === 0) {
                    console.log("add selected place to localstorage")
                    oldDataVilleDepart.push({
                        title: openedDropdown.villeDepart
                    })
                    localStorage.setItem('villeDepart', JSON.stringify(oldDataVilleDepart))
                } else {
                    console.log("already exist")
                }
            } else {
                console.log("oldDataVilleDepart is empty")
                oldDataVilleDepart.push({
                    title: openedDropdown.villeDepart
                })
                localStorage.setItem('villeDepart', JSON.stringify(oldDataVilleDepart))
            }

            // ********* ville d'arrivee *************
            var checkValue2 = 0
            if (oldDataVilleDarrivee.length !== 0) {
                console.log("oldDataVilleDarrivee is full")
                for (let i = 0; i < oldDataVilleDarrivee.length; i++) {
                    console.log("checkValue2", checkValue2)
                    console.log("**************** ", openedDropdown.villeDArrive, "****** ", oldDataVilleDarrivee[i].title.toLowerCase())
                    if (oldDataVilleDarrivee[i].title.toLowerCase().includes(openedDropdown.villeDArrive)) {

                        console.log("localStorage doesn't include selected place")
                        checkValue2++
                        console.log("increment", checkValue2)
                    }
                }
                if (checkValue2 === 0) {
                    console.log("add selected place to localstorage")
                    oldDataVilleDarrivee.push({
                        title: openedDropdown.villeDArrive
                    })
                    localStorage.setItem('villeDArrive', JSON.stringify(oldDataVilleDarrivee))
                } else {
                    console.log("already exist")
                }
            } else {
                console.log("oldDataVilleDarrivee is empty")
                oldDataVilleDarrivee.push({
                    title: openedDropdown.villeDArrive
                })
                localStorage.setItem('villeDArrive', JSON.stringify(oldDataVilleDarrivee))
            }
        } else {
            // show that feilds : villes depart, villes d'arrivee are required
            setShowRequiredMsg(true)
        }

    }
    const handleClickInput = (id) => {
        // console.log(id)
        if (id === 0) {
            if (openedDropdown.villeDepart === '' && localStorage.getItem('villeDepart') !== null) {
                console.log(JSON.parse(localStorage.getItem('villeDepart')))
                setSuggestedVillesDepart(JSON.parse(localStorage.getItem('villeDepart')))
            }
        } else {
            if (openedDropdown.villeDArrive === '' && localStorage.getItem('villeDArrive') !== null) {
                console.log(JSON.parse(localStorage.getItem('villeDArrive')))
                setSuggestedVillesDarrive(JSON.parse(localStorage.getItem('villeDArrive')))
            }
        }


    }
    return (
        <div className='mt-[75px] banner w-full h-[calc(100vh-120px)] flex items-center justify-center '
            style={{
                background: "salmon",
                backgroundSize: "cover",
                backgroundPosition: "center",
                backgroundAttachment: "fixed"
            }}>
            <div style={{
                gap: showRequiredMsg
                    ? "8px"
                    : "28px"
            }}
                className="bannerContent flex flex-col justify-center  h-full sectionContainer    ">
                <p className="desc w-full lg:w-[75%] text-[27px] md:text-[35px] lg:text-[50px] text-white font-bold leading-[34px] md:leading-[43px]  lg:leading-[60px] ">
                    NABHORELAN, <br />
                    plus qun voyage, <br />
                    cest lclate *
                </p>
                {
                    axiosError.isThereIsError && axiosError.errorContent
                }
                <p style={{
                    display: showRequiredMsg
                        ? "flex"
                        : "none"
                }}
                    className='text-[red] font-medium text-sm capitalize transition-all '>villes de depart et villes d'arrive sont Champs obligatoires *</p>
                <form id='clickBox' className='searchForm  w-full h-max flex flex-wrap items-center relative justify-center' >

                    {
                        voyageSearchFields.map((item, i) => (
                            <div
                                onClick={() => setOpenedDropdown({ ...openedDropdown, ndropdown: i })}
                                key={i}
                                className={i === 3
                                    ? "searchItem  relative h-[70px] w-full lg:w-[calc(100%/5+30px)] "
                                    : "searchItem  relative h-[70px] w-full lg:w-[calc(100%/5-10px)] "}>
                                <div className={openedDropdown.ndropdown === i
                                    ? "searchTopContainer flex items-center h-full openedDropDown  cursor-pointer   "
                                    : "searchTopContainer flex items-center h-full cursor-pointer  "}>
                                    <div
                                        className="pl-[15px] pr-[5px] searchTopContent z-20 flex items-center gap-[10px]">
                                        <div className="icon">{item.icon}</div>

                                        {
                                            (i === 0 || i === 1)
                                                ? <input
                                                    onClick={() => handleClickInput(i)}
                                                    required={true}
                                                    type="search"
                                                    autoComplete="off"
                                                    className='capitalize text-[15px] mr-1 w-full bg-transparent text-white placeholder:text-white placeholder:font-medium cursor-pointer'
                                                    name={item.name}
                                                    onChange={handleChange}
                                                    value={i === 0 ? openedDropdown.villeDepart : openedDropdown.villeDArrive}
                                                    placeholder={i === 0 ? "ville de dpart" : "ville d'arrive"}
                                                />
                                                : <div className="capitalize text-[15px] text-white font-medium">{item.title}</div>

                                        }
                                        {/* <div className="capitalize text-[15px] font-bold">{item.title}</div> */}
                                    </div>
                                    {/* if    shwo dropdownConent
                                     else  if suggestedVilles.length!==0 
                                     */}
                                    {item.dropDownContent}
                                </div>
                            </div>
                        ))
                    }
                    <BtnSubmit
                        style={{
                            zIndex: "20",
                            padding: "20px",
                            width: "calc(100%/5)",
                            height: "70px",
                            justifyContent: "center",
                            backgroudColor: "red",
                            gap: "15px"
                        }}
                        btnIcon={<Search fill="white" className=" w-[18px] h-[18px]" />}
                        btnText="rechercher"
                        onClick={handleSubmit} />
                    <div className={"disponibleVoyages z-10 flex w-full  items-center justify-center "+(showDispoVoyage ? "show" : "")}>
                        <div className="  p-3 sm:px-[60px] lg:px-5 dispoVoyContent relative border-[2px] w-full   xl:w-[70%]">
                            <div onClick={() => setShowDispoVoyage(false)} className="iconClose absolute cursor-pointer right-5 top-5 p-[5px] bg-mainColor w-8 h-8 flex items-center justify-center "><Close className="w-5 h-5 fill-white" /> </div>
                            <h2 className="title text-center mb-[30px] mt-[40px] text-xl sm:text-[25px]  font-[500] ">Et voici les voyages disponibles!!!</h2>
                            <div className="voyageList sm:p-5 h-[600px] overflow-hidden overflow-y-scroll w-full flex-col flex items-center gap-5 ">
                                {
                                    voyageDispoData.map((item, i) => (
                                        <div key={i} className="voyageItem w-full  lg:gap-0 lg:h-[140px] flex flex-col lg:flex-row items-center justify-between bg-mainColorLighter rounded-2xl p-5">
                                            <div className="left border-b-[1px] border-[lightgray] pb-[15px] lg:border-b-0 lg:pb-0 flex flex-col justify-between  w-full lg:w-[calc(100%/3-50px)] relative h-full ">
                                                <div className="flex relative row row-1 justify-between">
                                                    <div className="dateDepart font-bold text-[13px] ">{item.heureDepart}</div>
                                                    <div className="villeDepart font-bold text-[13px]  flex flex-end">{item.villeDepart}</div>
                                                </div>
                                                <div className="flex flex-col items-end text-xs  xl:pr-4 gap-2">
                                                    <span>Kindia</span>
                                                    <span>Kindia</span>
                                                </div>
                                                <div className="line absolute  w-[1px] h-[calc(100%-34px)] bg-black left-[50%] top-[50%] translate-x-[-50%] translate-y-[-50%] "></div>
                                                <div className="flex relative row row-2 justify-between">
                                                    <div className="dateDarrive font-bold text-[13px] ">{item.heureDarrivee}</div>
                                                    <div className="villeDarrive font-bold text-[13px] ">{item.villeDarrivee}</div>
                                                </div>
                                            </div>
                                            <div className="center border-b-[1px] border-[lightgray] py-[15px] lg:border-b-0 lg:py-0 h-full w-full lg:w-[calc(100%/3-50px)] flex flex-col gap-5 lg:gap-0  items-center justify-between">
                                                <div className="centerTop flex items-center gap-5">
                                                    <div className="imgContainer rounded-full w-[45px] h-[45px] "><img className='' src={PF + "imgs/Nabhorelan-headerbg.png"} alt="" /></div>
                                                    <div className="info w-[calc(100%-65px)]">
                                                        <div className="flex flex-col">
                                                            <div className="infoChauffeur font-bold text-[13px] ">David <sub className="age font-normal text-[9px]">(23 ans)</sub></div>
                                                            <div className="review flex items-center gap-1 text-[10px] font-semibold "><div className="icon"><Star className="fill-gold w-4 h-4" /> </div> 3.5</div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="centerBottom">
                                                    <div className="flex items-center justify-center gap-3">
                                                        <div className="icons flex items-center justify-center gap-1">
                                                            <Seat className="fill-mainColorAlt w-[20px] h-[20px]" />
                                                            <Seat className="fill-mainColorAlt w-[20px] h-[20px]" />
                                                            <Seat className=" w-[20px] h-[20px]" />
                                                            <Seat className=" w-[20px] h-[20px]" />
                                                        </div>
                                                        <span className="nbplacesRestants text-xs font-semibold">2 places restantes</span>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="right pt-[15px]  lg:pt-0 w-full h-full lg:w-max flex flex-col gap-5 lg:gap-0 items-center lg:items-end justify-between">
                                                <div className="priceInfo font-medium text-[18px]"><span className='font-bold text-[20px] text-mainColor '>250000</span> <sub>GNF/place</sub></div>
                                                <BtnLink style={{ width: "100%", justifyContent: "center", textAlign: "center" }} btnText="rserver" path="/" />
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        </div>
                    </div>
                </form>


            </div>
        </div>
    )
}

export default BannerHomePage