import React from 'react'
const BtnSubmit = ({btnIcon,btnText,style,onClick,custumStyle}) => {
  return (
    <button 
    onClick={onClick} 
    className={"bg-mainColor hover:bg-mainColorAlt first-letter:uppercase text-white cursor-pointer btnAction flex items-center md:gap-1 lg:gap-2 md:p-1 lg:p-2  font-medium "+custumStyle}
    style={style} >
      <div className="btnIcon">{btnIcon}</div> 
      {btnText}
      </button>
  )
}

export default BtnSubmit